import React from 'react';
import './App.css';

import Speaker from "./components/Speaker.js";
import Flower from "./components/Flower.js";
import Option from "./components/Option.js";
import Phase from "./components/Phase.js";

import phaseLogic from "./components/logic/phaseLogic.js";

class App extends React.Component {
  constructor() {
    super()

    //Date stats
    this.seeking = [0, 0]
    this.contestants = []
    this.date = 0
    this.QPD = 3
    this.questions = []

    this.state = {
      name: "Tulip",
      speak: "Hello petal! I have a favor to ask. Will you help me find a knight in shining cellulose?",
      species: "tulip",
      frame: 0,
      phase: 0,
    }
  }

  //Changes phase after buttons are selected
  changePhase = (event) => {
    phaseLogic(event.target.value, this)
  }

  componentDidMount() {
    setInterval(() => this.setState(prevState => { return { frame: prevState.frame + 1.5 } }), 1000 / 60)
  }

  render() {
    return (
      <div>
        <Speaker name={this.state.name.toUpperCase()} speak={this.state.speak} typewrite={this.state.frame} />
        <div className="centerFlex">
          <Flower species={this.state.species} idle={this.state.frame >= this.state.speak.length} animate={this.state.frame} />
          <Option object={this}/>
        </div>
        <Phase phase={this.state.phase}/>
      </div>
    );
  }
}
export default App;
