import questionList from "./questionList.json"

function randomizeQuestions(object) {
  object.questions = [];

  while (object.questions.length < 4) {
    let questionAdd = Math.floor(Math.random()*questionList.length)
      while (object.questions.indexOf(questionAdd) !== -1) {
        questionAdd = Math.floor(Math.random()*questionList.length)
      }
    object.questions.push(questionAdd)
  }
}

function changePhase(input, object) {
  let flowers = ["violet", "chrysanth", "gladiolus", "pansy", "iris", "lantana", "lily", "rose"]
  let suffixes = ["sy", "ly", "et", "lia", "orn", "mum", "ica", "us"]

    switch (input) {
      case "Accept":
        object.seeking = [Math.floor(Math.random()*8), Math.floor(Math.random()*8)]

        let phrases = ["gloomy", "cute", "earthy", "sunny", "bluish", "burnt", "bright", "dark"]
        let personas = ["organized", "loving", "competitive", "artistic", "smart", "loyal", "fun", "tough"]
        let requestMake = "I have 8 flowers here! I'm looking for a "+phrases[object.seeking[0]]+" "+personas[object.seeking[1]]+" flower, and I want you to find them."

        object.setState({
          speak: requestMake,
          frame: 0,
          phase: 1
        })
      break

      case "Start":
        object.contestants = [];
          for (let flowerSetup = 0; flowerSetup < 8; flowerSetup++) {
            object.contestants.push([Math.floor(Math.random()*8),Math.floor(Math.random()*8)])
            object.contestants[flowerSetup][2] = (flowers[object.contestants[flowerSetup][0]].substring(0,3)+suffixes[Math.floor(Math.random()*8)]).toUpperCase()
          }

        let perfectMatch = Math.floor(Math.random()*8)
        object.contestants[perfectMatch] = [object.seeking[0],object.seeking[1],""]
        object.contestants[perfectMatch][2] = (flowers[object.contestants[perfectMatch][0]].substring(0,3)+suffixes[Math.floor(Math.random()*8)]).toUpperCase()  

        object.date = 0
        object.QPD = 1
        object.setState({
          name: object.contestants[object.date][2],
          speak: "Hello!",
          species: flowers[object.contestants[object.date][0]],
          frame: 0,
          phase: 2
        })
        
        randomizeQuestions(object)
      break

      case "Continue":
        if (object.QPD === 0 && object.date < object.contestants.length - 1 && object.contestants.length > 2) {
          object.date += 1
          object.setState({
            name: object.contestants[object.date][2],
            speak: object.contestants.length < 8 ? "Hello again!" : "Hello!",
            species: flowers[object.contestants[object.date][0]],
            frame: 0,
            phase: 2
          })
          object.QPD = object.contestants.length === 4 ? 3 : object.contestants.length === 6 ? 2 : 1
        } else if (object.QPD === 0 && object.date === object.contestants.length - 1) {
          object.setState({
            name: "Tulip",
            speak: "Time to make a decision. Choose who to eliminate.",
            species: "tulip",
            frame: 0,
            phase: 4
          })
        } else {
          object.setState({
            name: "Tulip",
            speak: "Well petal, it's time for the final elimination. When you're ready.",
            species: "tulip",
            frame: 0,
            phase: 4
          })
        }
      break

      case "Results":
        let points = 0;

        if (object.contestants[0][0] === object.seeking[0] && object.contestants[0][1] === object.seeking[1]) {
          points = 2
          new Audio("./assets/sfx/perfect.mp3").play()
        } else if (object.contestants[0][0] === object.seeking[0] || object.contestants[0][1] === object.seeking[1]) {
          points = 1
          new Audio("./assets/sfx/good.mp3").play()
        } else {
          new Audio("./assets/sfx/fail.mp3").play()
        }

        object.setState({
          name: points === 2 ? "Perfect Match" : points === 1 ? "Good Match" : "Terrible Match",
          speak: points === 2 ? object.contestants[0][2]+" and I were meant to be!" : points === 1 ? object.contestants[0][2]+" could be a good friend!" : "You know? You tried your best.",
          species: "tulip",
          frame: 0,
          phase: 7
        })
      break

      case "Restart":
        object.setState({
          name: "Tulip",
          speak: "Hello petal! I have a favor to ask. Will you help me find a knight in shining cellulose?",
          species: "tulip",
          frame: 0,
          phase: 0,
        })
      break

      default:
        if (object.state.phase === 2) {
          object.QPD -= 1
          object.setState({
            speak: questionList[object.questions[input]][1][object.contestants[object.date][1]],
            frame: 0,
            phase: object.QPD === 0 ? 3 : 2
          })
          randomizeQuestions(object)
        } else if (object.state.phase === 4) {
          if (object.contestants.length > 2) {
            object.contestants[input] = "Delete"
            new Audio("./assets/sfx/eliminate.mp3").play()

            object.setState({
              speak: "Now another!",
              frame: 0,
              phase: 5
            })
          } else {
            object.contestants.splice(input, 1)

            object.setState({
              speak: "...",
              frame: 0,
              phase: 6
            })

          }
        } else if (object.state.phase === 5) {
          object.contestants[input] = "Delete"
          new Audio("./assets/sfx/eliminate.mp3").play()
          object.contestants.splice(object.contestants.indexOf("Delete"), 1)
          object.contestants.splice(object.contestants.indexOf("Delete"), 1)

          object.setState({
            speak: "Thanks petal!",
            frame: 0,
            phase: 3
          })
          object.date = -1;
        }
    }
}

export default changePhase