import questionList from "./questionList.json"

function optionSelection(object) {
    switch (object.state.phase) {
      case 0:
        return (
          <div className="optionFlex">
            <button onClick={object.changePhase} value="Accept">Yes!</button>
          </div>
        )

      case 1:
          return (
            <div className="optionFlex">
              <button onClick={object.changePhase} value="Start">I accept</button>
            </div>
          )

      case 2:
          return (
            <div className="optionFlex">
              <button onClick={object.changePhase} value={0}>{questionList[object.questions[0]][0]}</button>
              <button onClick={object.changePhase} value={1}>{questionList[object.questions[1]][0]}</button>
              <button onClick={object.changePhase} value={2}>{questionList[object.questions[2]][0]}</button>
              <button onClick={object.changePhase} value={3}>{questionList[object.questions[3]][0]}</button>
            </div>
          )

      case 3:
          return (
            <div className="optionFlex">
              <button onClick={object.changePhase} value="Continue">Continue</button>
            </div>
          )

      case 4:
        let round1 = object.contestants.length === 8 ? [0,1,2,3] : object.contestants.length === 6 ? [0,1,2,null] : [0,1,null,null]

        return (
          <div className="optionFlex">
            <button onClick={object.changePhase} value={round1[0]}>{object.contestants[round1[0]][2]}</button>
            <button onClick={object.changePhase} value={round1[1]}>{object.contestants[round1[1]][2]}</button>
            {round1[2] === null ? null : <button onClick={object.changePhase} value={round1[2]}>{object.contestants[round1[2]][2]}</button>}
            {round1[3] === null ? null : <button onClick={object.changePhase} value={3}>{object.contestants[round1[3]][2]}</button>}
          </div>
        )

      case 5:
        let round2 = object.contestants.length === 8 ? [4,5,6,7] : object.contestants.length === 6 ? [3,4,5,null] : [2,3,null,null] 
        
        return (
            <div className="optionFlex">
              <button onClick={object.changePhase} value={round2[0]}>{object.contestants[round2[0]][2]}</button>
              <button onClick={object.changePhase} value={round2[1]}>{object.contestants[round2[1]][2]}</button>
              {round2[2] === null ? null : <button onClick={object.changePhase} value={round2[2]}>{object.contestants[round2[2]][2]}</button>}
              {round2[3] === null ? null : <button onClick={object.changePhase} value={round2[3]}>{object.contestants[round2[3]][2]}</button>}
            </div>
        )
      
      case 6:
        return (
          <div className="optionFlex">
            <button onClick={object.changePhase} value="Results">???</button>
          </div>
        )

      case 7:
        return (
          <div className="optionFlex">
            <button onClick={object.changePhase} value="Restart">Restart</button>
          </div>
        )

      default:
        return (
          <div className="optionFlex"></div>
        )
    }
  }
  
  export default optionSelection