import React from 'react';

function Flower(props) {
    let plantType = String("./assets/flowers/"+(props.species)+(props.idle ? "Idle" : "Talk")+(props.animate % 20 < 10 ? 1 : 0) +".png")
    
    //Plants play one out of three random sounds every second
    if (!props.idle && props.animate % 60 === 0) {
    const plantSound = new Audio("./assets/sfx/laugh"+(Math.floor(Math.random()*3))+".mp3")
    plantSound.play()
    }

    return (
      <div className="flower">
        <img className="fullFlower" src={plantType} alt="flower"/>
      </div>
    );
}

export default Flower;